.main{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex: 1;
    height: 100vh;

}
.containers{
    height: 300px;
    width: 400px;
    background: whitesmoke;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    border-radius: 10px;
}